<template>
  <div class="mobileCall">
      <div class="titleWarp">
          <h1 class="pageTitle">{{ pageTitle }}</h1>
          <div class="userWarp">
              <div class="userImgWarp">
                  <img class="userImg" src="../../assets/user1.jpg" alt="missing">
              </div>
              <div class="userText">
                  <p class="userT">部门：<span style="font-weight: lighter;">{{ user.theme }}</span></p>
                  <p class="userT">姓名：<span style="font-weight: lighter;">{{ user.name }}</span></p>
              </div>
              <div class="outBtnWarp">
                  <el-button @click="outBtnFn" type="text" class="outBtn">
                      <svg-icon icon-class="signOut" class="signOutIcon"></svg-icon>
                  </el-button>
              </div>
          </div>
      </div>
    <div class="showArea">
      <el-row :gutter="20">
        <el-col :span="6"  v-for="(item, index) in todayList" :key="index" >
          <div class="grid-content bg-purple">{{ item.name }}</div>
          <div>{{ item.val }}</div>
        </el-col>
      </el-row>
    </div>
    <div class="marqueeWarp">
      <marquee behavior="alternate" scrollamount="3" v-html="marqueeText"></marquee>
    </div>
    <div class="inputWarp"  v-if="false">
      <el-row :gutter="20">
        <el-col :span="12" v-for="(item, index) in todayList" :key="index" class="itemTodayIfon">
          <el-input placeholder="" v-model="item.val" disabled>
            <template slot="prepend">{{ item.name }}</template>
          </el-input>
        </el-col>
        <el-col :span="12" class="itemTodayIfon">
          <el-input placeholder="输入申请方" v-model="applyName" :disabled="openApplyName">
            <template slot="prepend">申请方</template>
          </el-input>
        </el-col>
        <el-col :span="12" class="itemTodayIfon">
          <el-input placeholder="输入申请项目" v-model="applyObject" :disabled="openApplyObject">
            <template slot="prepend">申请项目</template>
          </el-input>
        </el-col>
      </el-row>
    </div>
    <div class="btnWarp">
      <el-row :gutter="20">
        <el-col :span="8" class="itemBtnWarp">
          <el-button class="itemBtn" type="info" @click="nextPersonFn" :disabled="nextPerson.disabled">
              <svg-icon :icon-class="nextPerson.icon" class="btnSvg"></svg-icon>
          </el-button>
            <div class="btnBottomText">{{ nextPerson.name }}</div>
        </el-col>
        <el-col :span="8" class="itemBtnWarp">
          <el-button class="itemBtn" type="info" @click="reCallFn" :disabled="reCall.disabled">
              <svg-icon :icon-class="reCall.icon" class="btnSvg"></svg-icon>
          </el-button>
            <div class="btnBottomText">{{ reCall.name }}</div>
        </el-col>
        <el-col :span="8" class="itemBtnWarp">
          <el-button class="itemBtn" type="info" @click="pauseFn" :disabled="pause.disabled">
              <svg-icon :icon-class="pause.icon" class="btnSvg"></svg-icon>
          </el-button>
            <div class="btnBottomText">{{ pause.name }}</div>
        </el-col>
        <el-col :span="8" class="itemBtnWarp">
          <el-button class="itemBtn" type="info" @click="moveFn" :disabled="move.disabled">
              <svg-icon :icon-class="move.icon" class="btnSvg"></svg-icon>
          </el-button>
            <div class="btnBottomText">{{ move.name }}</div>
        </el-col>
        <el-col :span="8" class="itemBtnWarp">
          <el-button class="itemBtn" type="info" @click="AcceptanceFn" :disabled="!openAcceptance">
              <svg-icon :icon-class="Acceptance.icon" class="btnSvg"></svg-icon>
          </el-button>
            <div class="btnBottomText">{{ Acceptance.name }}</div>
        </el-col>
        <el-col :span="8" class="itemBtnWarp">
          <el-button class="itemBtn" type="info" @click="unAcceptanceFn" :disabled="unAcceptance.disabled">
              <svg-icon :icon-class="unAcceptance.icon" class="btnSvg"></svg-icon>
          </el-button>
            <div class="btnBottomText">{{ unAcceptance.name }}</div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import lodash from 'lodash'
export default {
  name: 'call',
  data () {
    return {
      pageTitle: '新津政务中心排队叫号系统',
      userIfon: {},
      user: {
        theme: '',
        name: ''
      },
      marqueeText: '',
      initialMarqueeText: '',
      todayList: [
        {
          name: '当前窗口号',
          val: localStorage.window
        },
        {
          name: '当前办理号',
          val: ''
        },
        {
          name: '等待办理人数',
          val: ''
        },
        {
          name: '当天办理总数',
          val: '0'
        }
      ],
      applyName: '',
      openApplyName: true,
      applyObject: '',
      openApplyObject: true,
      nextPerson: {
        name: '下一位',
        icon: 'user',
        disabled: false
      },
      reCall: {
        name: '重新呼叫',
        icon: 'horn',
        disabled: true
      },
      pause: {
        name: '暂停',
        icon: 'pause',
        disabled: false
      },
      isPause: true,
      move: {
        name: '转号',
        icon: 'move',
        disabled: true
      },
      Acceptance: {
        name: '受理',
        icon: 'Acceptance',
        disabled: true
      },
      unAcceptance: {
        name: '未受理',
        icon: 'unAcceptance',
        disabled: true
      },
      personInfo: {}
    }
  },
  computed: {
    openAcceptance: function () {
      return (this.applyName.replace(/(^\s*)|(\s*$)/g, '') !== '' && this.applyObject.replace(/(^\s*)|(\s*$)/g, '') !== '')
    }
  },
  watch: {
    openAcceptance (val) {
      if (val) {
        this.Acceptance.disabled = false
      } else {
        this.Acceptance.disabled = true
      }
    }
  },
  created: function () {
    if (sessionStorage.userInfo) {
      this.userIfon = JSON.parse(sessionStorage.userInfo)
      this.marqueeText = '欢迎 ' + this.userIfon.name + ' 登录系统'
      this.initialMarqueeText = '欢迎 ' + this.userIfon.name + ' 登录系统'
      this.user = {
        theme: this.userIfon.subject,
        name: this.userIfon.name
      }
    } else {
      console.log('call-无用户信息')
    }
  },
  methods: {
    // 点击事件：600毫秒内，以最后的为准
    nextPersonFn: lodash.debounce(function () {
      this.callNextPersonFn()
    }, 1000),
    reCallFn: lodash.debounce(function () {
      this.reCallHandle()
    }, 1000),
    pauseFn: lodash.debounce(function () {
      if (this.isPause) { // 暂停
        this.pause = {
          name: '开始',
          icon: 'start',
          disabled: false
        }
        this.pauseHandleFn()
        let list = [
          true, true, false,
          true, true, true
        ]
        this.distributeDisabled(list, this)
      } else { // 开始
        this.pause = {
          name: '暂停',
          icon: 'pause',
          disabled: false
        }
        this.startHandleFn()
        let list = [
          false, true, false,
          true, true, true
        ]
        this.distributeDisabled(list, this)
      }
      this.isPause = !this.isPause
    }, 600),
    moveFn: lodash.debounce(function () {
      let reference = JSON.parse(sessionStorage.reference)
      console.log(reference)
      console.log(this.userIfon)
      if (reference.radio === '3') {
        if (reference.curTheme === '') {
          this.$alert('请选择转号的主题', '提示', {
            confirmButtonText: '确定',
            callback: action => {
            }
          })
        } else if (reference.curMatter === '') {
          this.$alert('请选择转号的事项', '提示', {
            confirmButtonText: '确定',
            callback: action => {
            }
          })
        } else if (reference.curTheme === this.userIfon.subject) {
          this.$alert('转号的主题与工作人员所在主题一致，请修改', '提示', {
            confirmButtonText: '确定',
            callback: action => {
            }
          })
        } else { // 转号
          this.moveHandleFn(reference)
        }
      } else {
        this.$alert('请选择转号功能', '提示', {
          confirmButtonText: '确定',
          callback: action => {
          }
        })
      }
    }, 600),
    AcceptanceFn: lodash.debounce(function () {
      let referenceObj = JSON.parse(sessionStorage.reference)
      if (referenceObj.selectionElementId.length) {
        if (referenceObj.selectionElementId.length < referenceObj.allID.length) {
          this.myAlert('审查要素未核对完', '提示')
        } else {
          this.$alert('请确定当前事项合格', '提示', {
            confirmButtonText: '确定',
            callback: action => {
              let reference = JSON.parse(sessionStorage.reference)
              if (reference.radio === '2') {
                this.personInfo.subject = reference.curTheme
                this.personInfo.item = reference.curMatter
              }
              this.personInfo.deal_status = 'Y'
              this.AcceptanceHandleFn()
            }
          })
        }
      } else {
        this.myAlert('请核对审查要素', '提示')
      }
    }, 600),
    unAcceptanceFn: lodash.debounce(function () {
      this.$alert('请确定当前事项不合格', '提示', {
        confirmButtonText: '确定',
        callback: action => {
          let reference = JSON.parse(sessionStorage.reference)
          if (reference.radio === '2') {
            this.personInfo.subject = reference.curTheme
            this.personInfo.item = reference.curMatter
          }
          this.personInfo.deal_status = 'N'
          this.AcceptanceHandleFn()
        }
      })
    }, 600),
    outBtnFn: lodash.debounce(function () {
      this.$confirm('确认是否退出？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        if (sessionStorage.user_id) {
          this.signOutFn({
            user_id: sessionStorage.user_id
          })
        } else {
          this.notifyFn('提示', '用户未登陆')
          this.$router.push('/mobileSign')
        }
      }).catch(() => {
      })
    }, 600),
    async signOutFn (params) {
      try {
        let res = await this.$api.unsplash.signOut(params)
        console.log(res)
        if (res.errno === '0') {
          sessionStorage.clear()
          this.$router.push('/mobileSign')
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async callNextPersonFn () {
      try {
        let params = this.userIfon
        let res = await this.$api.unsplash.getNextPerson(params)
        console.log(res)
        if (res.errno === '0') {
          // 有人就放数据
          this.personInfo = res.data.business
          this.personInfo.user_id = this.userIfon.user_id
          this.personInfo.lingpai = localStorage.lingpai
          this.todayList[1].val = this.personInfo.number
          this.todayList[2].val = res.data.wait
          this.todayList[3].val = res.data.called
          this.applyName = ''
          this.applyObject = ''
          this.openApplyName = false
          this.openApplyObject = false
          if (this.personInfo.cometimes + 1 >= 3) {
            this.marqueeText = `流水号:<b>` + this.personInfo.water_num + `</b>，本次为<b style="color:red;">第` + (this.personInfo.cometimes + 1) + `次</b>来办理<b>` + this.personInfo.item + `</b>，<b style="color:red;">请认真对待每位群众</b>`
          } else {
            this.marqueeText = `流水号:<b>` + this.personInfo.water_num + `</b>，本次为第` + (this.personInfo.cometimes + 1) + `次来办理<b>` + this.personInfo.item + `</b>，<b style="">请认真对待每位群众</b>`
          }
          // 初始化参考各项
          this.getElement(this.personInfo.item)
          // 按钮锁
          let list = [
            true, false, true,
            false, true, false
          ]
          this.distributeDisabled(list, this)
        } else if (res.errno === '4004') {
          this.todayList[1].val = ''
          this.notifyFn('提示', res.errmsg)
          // 按钮锁
          let list = [
            false, true, false,
            true, true, true
          ]
          this.distributeDisabled(list, this)
        } else {
          this.todayList[1].val = ''
          this.notifyFn('提示', res.errmsg)
          // 按钮锁
          let list = [
            false, true, false,
            true, true, true
          ]
          this.distributeDisabled(list, this)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        // 按钮锁
        let list = [
          false, true, false,
          true, true, true
        ]
        this.distributeDisabled(list, this)
        console.log(e)
      }
    },
    async getElement (curMatter) {
      try {
        let params = {
          item: curMatter
        }
        let res = await this.$api.unsplash.postElements(params)
        if (res.errno === '0') {
          let temp = res.data
          let allID = []
          for (let i = 0; i < temp.length; i++) {
            temp[i].index = i + 1
            allID.push(temp[i].id)
          }
          let reference = {
            radio: '1',
            curTheme: this.personInfo.subject,
            curMatter: this.personInfo.item,
            allID: allID,
            selectionElementId: this.personInfo.last_reviews ? this.personInfo.last_reviews.split('-') : []
          }
          sessionStorage.reference = JSON.stringify(reference)
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async reCallHandle () {
      try {
        let params = {
          water_num: this.personInfo.water_num,
          lingpai: localStorage.lingpai,
          user_id: sessionStorage.user_id
        }
        let res = await this.$api.unsplash.postReCall(params)
        if (res.errno === '0') {
          this.notifyFn('提示', '正在重呼', 'success')
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async pauseHandleFn () {
      try {
        let params = {
          lingpai: localStorage.lingpai,
          user_id: sessionStorage.user_id
        }
        let res = await this.$api.unsplash.postPause(params)
        if (res.errno === '0') {
          this.afterDealed(this)
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async startHandleFn () {
      try {
        let params = {
          lingpai: localStorage.lingpai,
          user_id: sessionStorage.user_id
        }
        let res = await this.$api.unsplash.postStart(params)
        if (res.errno === '0') {
          this.afterDealed(this)
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async moveHandleFn (reference) {
      try {
        let params = this.personInfo
        params.subject = reference.curTheme
        params.item = reference.curMatter
        console.log(this.personInfo)
        let res = await this.$api.unsplash.postMove(params)
        console.log(res)
        if (res.errno === '0') {
          this.afterDealed(this)
          let list = [
            false, true, false,
            true, true, true
          ]
          this.distributeDisabled(list, this)
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async AcceptanceHandleFn () {
      try {
        let referenceObj = JSON.parse(sessionStorage.reference)
        this.personInfo.reviews = referenceObj.selectionElementId.join('-')
        this.personInfo.p_name = this.applyName.replace(/(^\s*)|(\s*$)/g, '')
        this.personInfo.p_project = this.applyObject.replace(/(^\s*)|(\s*$)/g, '')
        let params = this.personInfo
        console.log(this.personInfo)
        let res = await this.$api.unsplash.postAcceptance(params)
        console.log(res)
        if (res.errno === '0') {
          this.afterDealed(this)
          let list = [
            false, true, false,
            true, true, true
          ]
          this.distributeDisabled(list, this)
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    notifyFn (title, msg, type = 'warning') {
      this.$notify({
        title: title,
        message: msg,
        type: type,
        dangerouslyUseHTMLString: true,
        duration: 5000,
        position: 'top-right'
      })
    },
    myAlert (txt, title) {
      this.$alert(txt, title, {
        confirmButtonText: '确定',
        callback: action => {
        }
      })
    },
    distributeDisabled (list, self) {
      self.nextPerson.disabled = list[0]
      self.reCall.disabled = list[1]
      self.pause.disabled = list[2]
      self.move.disabled = list[3]
      self.Acceptance.disabled = list[4]
      self.unAcceptance.disabled = list[5]
    },
    afterDealed (self) {
      self.todayList[1].val = ''
      self.applyName = ''
      self.openApplyName = true
      self.applyObject = ''
      self.openApplyObject = true
    }
  }
  // beforeRouteLeave (to, from, next) {
  //   // 设置下一个路由的 meta
  //   // to.meta.keepAlive = true // 不刷新
  //   // next()
  // }
}
</script>

