            margin-top: 2vh;
            padding: 0 40px;
        }
    }
    .inputWarp{
        width: 1200px;
        /*height: 800px;*/
        margin: 0 auto;
        padding: 40px 10px 40px 10px;
        .itemTodayIfon{
            margin-bottom: 20px;
            &:nth-last-child(1),&:nth-last-child(2){
