    }
    .btnWarp{
        width: 100vw;
        /*height: 800px;*/
        margin: 0 auto;
        padding: 40px;
        /*background: #ffae7e;*/
        .itemBtnWarp{
            /*height: 100px;*/
            margin-bottom: 30px;
            &:nth-last-child(1),&:nth-last-child(2){
                margin-bottom: 0;
            }
            .itemBtn{
                width: 100%;
                font-size: 30px;
                color: #ffffff;
                .btnSvg{
                    width: 30px;
                    height: 30px;
                }
            }
            .btnBottomText{
                margin-top: 10px;
            }
        }
    }
</style>



